import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NavigationService } from 'src/app/navigation.service';
import {ImmigrationQuestion} from './immigration';

@Component({
  selector: 'app-immigration',
  templateUrl: './immigration.component.html',
  styleUrls: ['./immigration.component.css']
})
export class ImmigrationComponent implements OnInit {

  question : string;
  progress: any;
  previousUrl: any;
  immigrationArray = ImmigrationQuestion;
  
  constructor(private router: Router, private activatedRoute: ActivatedRoute,private ns: NavigationService) {
    console.log('prev', this.ns.getPreviousUrl())
    this.activatedRoute.params.subscribe(params => {
      console.log('pa', params.id.slice(0,-2));
      this.previousUrl = params.id.slice(0,-2);
      if (params.id == 1) {
        this.question = this.immigrationArray[0].i1.question;
        this.progress = this.immigrationArray[0].progress;
        this.optionsArray = this.immigrationArray[0].i1.options;
      }
      else if (params.id == 1.1) {
        this.question = this.immigrationArray[1].i1a.question;
        this.progress = this.immigrationArray[1].progress;
        this.optionsArray = this.immigrationArray[1].i1a.options;
      }
      else if(params.id == '1.1.1') {
        this.question = this.immigrationArray[2].i1b.question;
        this.progress = this.immigrationArray[2].progress;
        this.optionsArray = this.immigrationArray[2].i1b.options;
      }      
    });

    this.setClickedRow = function (index, link) {
      console.log('dddd', link);
      this.nextButtonCssFlag = true;
      this.nextButtonDisableFlag = false;
      this.selectedLink = link;
      this.selectedRow = index;
    }
  }

  cssFlag = false;
  optionsArray: any[];
  setClickedRow: Function;
  selectedRow: number;
  selectedLink: string;
  nextButtonFlag = true;




  previousUrlFunction() {
    console.log('null', this.previousUrl)

    if(this.previousUrl == null || this.previousUrl == undefined || this.previousUrl == ''){
      this.router.navigate(['/assistance'])
    }
    else {
    this.router.navigate(['/assistance/traffic-offense/', this.previousUrl],{ skipLocationChange: true })
    } 
  }


  navigateTo() {
    if(this.selectedLink == 'final') {
      console.log('i am email')
        this.router.navigate(['/assistance/email'])
    } 
    else {
    this.router.navigateByUrl('/', { skipLocationChange: true }).then(() => {
      this.router.navigate(['/assistance/immigration/', this.selectedLink], { skipLocationChange: true })       
      }
  
    
    );
  }}


  ngOnInit() {
  }

}
