import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AssistanceTypeComponent } from './assistance-type/assistance-type.component';
import { Routes, RouterModule } from '@angular/router';
import { TrafficOffenseComponent } from './traffic-offense/traffic-offense.component';
import { ImmigrationComponent } from './immigration/immigration.component';
import { EmailSubmissionComponent } from './email-submission/email-submission.component';
import { WillsComponent } from './wills/wills.component';

const assistanceRouting: Routes = [
  {
  path: '', component: AssistanceTypeComponent, data: {animation: 'AssitanceType', flag:'false'}
  },
  {
  path: 'traffic-offense/:id', component: TrafficOffenseComponent, data: {animation: 'TrafficOffense', 
  flag:'false'}
  },
  {
  path: 'wills/:id', component: WillsComponent, data: {animation: 'Wills', 
  flag:'false'}
  },
  {
    path: 'immigration/:id', component: ImmigrationComponent, data: {animation: 'Immigration', flag:'false'}
    },
    {
      path: 'email', component: EmailSubmissionComponent, data: {animation: 'Immigration', flag:'false'}
      }
]

@NgModule({
  imports: [
    CommonModule, RouterModule.forChild(assistanceRouting)
  ],
  declarations: [AssistanceTypeComponent, TrafficOffenseComponent, ImmigrationComponent, EmailSubmissionComponent, WillsComponent]
})
export class AssistanceModule { }
